@import url(https://fonts.googleapis.com/css?family=Nunito);
@import url(https://fonts.googleapis.com/css?family=Nunito);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: 'Nunito', sans-serif;
}

.font30 {
    font-size: 30px;
}
.space20{
    padding-top: 20px;
}
.centered {
    text-align: center;
}
.jumbo-main {
  background-image: url(/newbegweb/static/media/sunrise.e8e80fa3.jpg);
  background-size: cover;
  height: 200px;
  position: relative;
  z-index: -2;
}

.signin_padding{
    padding-top: 0px;
    padding-bottom: 0px;
    margin-left:140px;
}

.navbar{
    position: -webkit-sticky;
    position: sticky; 
    top: 0; 
    box-shadow: 0 4px 2px -2px rgba(0,0,0,.2);
    background-color: white;
    z-index:3;
}

.widepic{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

.centeredpink {
    text-align: center;
    background-color: #F5B295;

}

.outline{
    padding-top:20px;
    padding-bottom: 20px;
    margin-left: auto;
    margin-right:auto;
    width:925px;
    background-color: rgb(248, 248, 248);
    border-radius: 10px;
    border: 3px solid rgb(189, 187, 187);
}
@media(max-width:992px){
    .outline{
        margin-left: 0px;
        margin-right:0px;
        width:100%;
    }
    .signin_padding{
        margin-left:20px;
    }
}
@media(max-width:768px){
    .login-style input{
        background: black;
        border: 0px;
        padding: 10px;
        width: 600px;
        border-radius:5px;
        max-width: 100%;
    }
}

.login-style input{
    background: white;
    border: 0px;
    padding: 10px;
    width: 600px;
    margin-left:155px;
    border-radius:5px;
    max-width: 100%;
}
.date-style{
    color:black;
}
input:-webkit-autofill{
    box-shadow: 0 0 0px 1000px white inset;
   
}

.sign-in{
    padding: 5px 20px 0px 20px;
    border-radius: 10px;
    border:0px;
    color: white;
    background-color:#009dff;
}
.sign-in:hover{
    background-color: rgb(95, 182, 253);
}
.login-style input:valid{
    border: 2px solid rgb(0, 201, 0);
    box-shadow: 0 0 0px 1000px white inset;
}
.login-style input:invalid{
    border: 2px solid red; 
}
.login-style input:hover{
    border: 2px solid rgb(43, 142, 255);
    border-radius:5px;
    box-shadow: 0 0 0px 1000px white inset;
}
.login-style input:focus{
    border: 2px solid rgb(43, 142, 255);
    box-shadow: 0 0 0px 1000px white inset;
    border-radius:5px;
    outline:none;
}

.signin-error{
    padding-left:140px;
    font-size:16px;
    color:red;
    text-align: left;
}
.signin-success{
    padding-left:140px;
    font-size:16px;
    color:green;
    text-align: left;
}
.createAcc-error{
    padding-left:140px;
    font-size:16px;
    color:red;
    text-align: left;
}
.email-error{
    padding-left:0px;
    font-size:16px;
    color:red;
    text-align: left;
}



body {
    font-family: 'Nunito', sans-serif;
}

.home-logo{ 
    position:relative;
    display: block;
    margin:auto;
    width: 100%;
    height: auto;
    margin-top:20px;
}

.home-textbox-1{
    display: inline-block;
    background-color: #F5B295;
    width: 100%;
    height: 100%;
    padding: 25px 25px;
    text-align: center;
    font-size: 22px;
    box-shadow: 20px 20px 5px #b8b2b2;

}
.home-textbox-mission{
    display: inline-block;
    background-color: #F5B295;
    width: 100%;
    padding: 15px 25px;
    text-align: center;
    font-size: 28px;
    box-shadow: 20px 20px 5px #b8b2b2;
    margin-top:20px;

}
.home-textbox-feedback{
    display: inline-block;
    background-color: #F5B295;
    width: 100%;
    padding: 15px 25px;
    text-align: center;
    font-size: 24px;
    box-shadow: 20px 20px 5px #b8b2b2;
}



.box-outline{
    padding-left: 30px;
    padding-right: 30px;
    padding-top:50px;
}
.feedback-outline{
    margin-top: 50px;
    margin-bottom: 50px;
    padding-left: 70px;
    padding-right: 100px;
    
}
.image-outline{
    padding-left: 50px;
    padding-right: 50px;
}
.pic-people{
    display: inline-block;
    background-color: #b8b2b2;
    width: 100%;
    height: auto;
    padding: 15px 25px;
    text-align: center;
    font-size: 20px;
    border: 3px solid rgb(128, 126, 126);
}

@media(max-width: 992px){
    .home-textbox-1{
        margin-top:50px;
        height:auto;
    }
    .home-textbox-mission{
        margin-top:50px;
    }
    .image-outline{
        margin-top:50px;
        padding-left:0px;
        padding-right:0px;
    }
    .feedback-outline{
        padding-left:50px;
        padding-right:50px;
        margin-bottom:100px;
        margin-top:100px;
    }
    .home-logo{
        width: 50%; 
    }

}
.home-textbox-feedback p{
    font-size: 22px;
}
@media(max-width:576px){
    .home-textbox-1{
        font-size: 18px;
    }
    .home-textbox-feedback p{
        font-size: 18px;
    }
    .home-textbox-mission{
        font-size: 22px;
    }
}

.home-padding{
    padding-right: 20px;
}
.space80{
    padding-top: 80px;
}
